.main-catalogue__list {
  grid-column: 2/-1;
  display: grid;
  grid-template-columns: repeat(3, minmax(317px, 1fr));
  grid-template-rows: repeat(2, 1fr);
  justify-content: space-between;
  gap: 30px;
  margin-top: 13px;
}
@media (max-width: 1439px) {
  .main-catalogue__list {
    grid-column: 1/-1;
    grid-row: 5/6;
    grid-template-columns: repeat(2, minmax(349px, 1fr));
    grid-template-rows: repeat(3, 1fr);
  }
}

.main-catalogue__item {
  padding: 50px 30px 15px;
  position: relative;
  background-color: #ffffff;
  position: relative;
  top: 0;
  transition: all 0.1s ease-in;
}
.main-catalogue__item:hover {
  z-index: 1;
  box-shadow: 0 5px 15px #333333;
  top: -2px;
}
.main-catalogue__item img {
  object-fit: cover;
  margin: 0 auto;
}
.main-catalogue__item h3 {
  font-size: 24px;
  font-weight: 700;
  line-height: 36px;
  letter-spacing: 0;
  margin: 30px 0 10px;
}
.main-catalogue__item p {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0;
  margin: 10px 0;
}
.main-catalogue__item div {
  display: grid;
  grid-template-columns: 1fr minmax(141px, 1fr);
  justify-content: space-between;
  align-items: center;
}
.main-catalogue__item span {
  font-size: 20px;
  font-weight: 600;
  line-height: 32px;
  letter-spacing: 0;
}
.main-catalogue__item button {
  padding: 7px;
  display: grid;
  grid-template-columns: repeat(2, auto);
  justify-content: center;
  gap: 6px;
}
.main-catalogue__item button span {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0;
}

/* NAVIGATION BLOCK*/
.main-catalogue__nav {
  grid-column: -1/1;
  display: flex;
  justify-self: end;
  width: fit-content;
  margin-top: 60px;
  margin-bottom: 60px;
}
@media (max-width: 1439px) {
  .main-catalogue__nav {
    grid-row: 6/7;
    margin-top: 70px;
    margin-bottom: 45px;
  }
}
.main-catalogue__nav button {
  border: none;
  cursor: pointer;
  border-radius: 4px;
}

.main-catalogue__button {
  padding: 9px 54px;
}

.main-catalogue__pagination {
  min-width: 40px;
  min-height: 40px;
  padding: 8px;
}
.main-catalogue__pagination--selected {
  margin-left: 20px;
  margin-right: 20px;
  background-color: transparent;
  color: #333333;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0;
}

/* FILTERS BLOCK */
.main-filters {
  grid-column: 1/2;
  grid-row: 3/4;
  position: relative;
}
@media (max-width: 1439px) {
  .main-filters {
    grid-column: 1/-1;
  }
}
@media (max-width: 1439px) {
  .main-filters form {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }
}
.main-filters fieldset {
  padding: 0;
  border: none;
}
.main-filters p {
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0;
  margin: 0 0 -8px 0;
}
.main-filters label {
  margin: 10px 0;
}
.main-filters span {
  position: absolute;
}

.main-filters__cell:first-child p {
  position: relative;
  top: -40px;
}
@media (max-width: 1439px) {
  .main-filters__cell:first-child p {
    top: 0;
  }
}

/* RANGE MAKUP*/
.main-filters__range-slider {
  position: relative;
  margin: 0 0 20px;
}
@media (max-width: 1439px) {
  .main-filters__range-slider {
    margin: 45px 0 20px;
  }
}

.main-filters__price-input {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 34px 0 10px;
}

.main-filters__field {
  display: flex;
  width: 134px;
  height: 40px;
  align-items: center;
}
@media (max-width: 1439px) {
  .main-filters__field {
    width: 92px;
  }
}

.main-filters__field input {
  width: 100%;
  height: 100%;
  outline: none;
  font-size: 19px;
  border-radius: 5px;
  text-align: center;
  border: 1px solid #9070ec;
  -moz-appearance: textfield;
  color: #bdbdbd;
}
.main-filters__field input:hover, .main-filters__field input:focus, .main-filters__field input:active {
  border: 1px solid #593cac;
  color: #333333;
}
.main-filters__field input:disabled {
  border: 1px solid #e2e2e2;
  color: #e2e2e2;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.main-filters__separator {
  width: 10px;
  height: 2px;
  background-color: #bdbdbd;
}

.main-filters__slider {
  display: flex;
  align-items: center;
  height: 2px;
  position: relative;
  background: #e2e2e2;
  border-radius: 5px;
}

.main-filters__progress {
  height: 4px;
  left: 0;
  right: 15%;
  position: absolute;
  border-radius: 5px;
  background: #9070ec;
}

.main-filters__range-input {
  position: relative;
}

.main-filters__range-input input {
  position: absolute;
  width: 100%;
  height: 5px;
  top: -5px;
  background: none;
  pointer-events: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}

