}
input[type=range]::-webkit-slider-thumb:disabled,
input[type=range]::-moz-range-thumb:disabled {
  background-image: url('data:image/svg+xml,%3Csvg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"%3E%3Ccircle cx="12" cy="12" r="11" fill="%23F8F8F8" stroke="%23BDBDBD" stroke-width="2"/%3E%3C/svg%3E%0A');
}

/* Buttons */
